    onScrollChange($e) {
      this.scrollTopValue = $e.target.scrollTop;
    },
  },
  activated() {
    this.$refs.goods.scrollTop = this.scrollTopValue;
  },
  watch: {
    // 监听排序方式
    sort: function () {
      this.setSortGoodsData();
    },

    //监听布局类型切换
    layoutType: function () {
      this.initLayout();
