<style lang="scss" scoped>
@import "@css/style.scss";

//商品公用样式
.goods {
  background-color: $bgColor;

  &-scroll {
    overflow: hidden;
    overflow-y: auto;
  }

  &-item {
    background-color: white;
    padding: $marginSize;
    box-sizing: border-box;

    &-desc {
      width: 100%;

      &-name {
        font-size: $infoSize;
        overflow: hidden;
        text-overflow: ellipsis;
        word-break: break-word;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        display: -webkit-box;
        line-height: px2rem(18);

        &-hint {
          color: $textHintColor;
        }
      }

      &-data {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: $marginSize;

        &-price {
          font-size: $titleSize;
          color: $mainColor;
          font-weight: 500;
        }

        &-volume {
          font-size: $infoSize;
          color: $textHintColor;
        }
      }
    }
  }
}

//网格瀑布流布局样式
.goods-waterfall {
  display: flex;
  flex-direction: row;
  justify-content: flex- start;
  flex-wrap: wrap;
  background-color: $bgColor;
  box-sizing: border-box;
  border: px2rem(3) #eeeeee solid;
  &-item {
    box-sizing: border-box;
    padding: px2rem(8);
    width: 50%;
    border: px2rem(4) #eeeeee solid;
    border-radius: 10px;
    .goods-item-img {
      width: 100%;
    }
  }
}

//列表布局
.goods-list {
  &-item {
    display: flex;
    border-bottom: 1px solid $lineColor;
    .goods-item-img {
      width: px2rem(120);
      height: px2rem(120);
    }
    .goods-item-desc {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: $marginSize;
    }
  }
}
</style>

